er>\n        </template>\n      </el-table-column>\n\n      <el-table-column label=\"申请时间\"\n                       prop=\"create_time\"\n                       align=\"center\"></el-table-column>\n      <el-table-column label=\"操作\"\n                       align=\"center\">\n        <template slot-scope=\"scope\">\n          <el-button type=\"primary\"\n                     size=\"small\"\n                     :loading=\"throughLoading[scope.$index]\"\n                     @click=\"through(scope.row,scope.$index)\">通过</el-button>\n          <el-button type=\"danger\"\n                     size=\"small\"\n                     :loading=\"rejectedLoading[scope.$index]\"\n                     @click=\"rejected(scope.row,scope.$index)\">驳回</el-button>\n        </template>\n      </el-table-column>\n    </el-table>\n\n    <!-- 油站图片详情 -->\n    <el-dialog title=\"油站图片\"\n               center\n               width=\"30%\"\n               :visible.sync=\"oliImgVisible\">\n      <div style=\"text-align:center\">\n        <img v-for=\"(item,index) in oliImgList\"\n             :key=\"index\"\n             :src=\"item\"\n             width=\"100px\"\n             height=\"100px\"\n             style=\"margin-right:10px\"\n             ref=\"oliImg\" />\n      </div>\n    </el-dialog>\n\n    <!-- 分页 -->\n    <div class=\"page_center\">\n      <paging :page-count=\"pageCount\"\n              :page=\"page\"\n              @index=\"(e)=>{\n                    page =e,\n                    init()\n                }\"></paging>\n      <!--分页的组件-->\n    </div>\n  </div>\n</template>\n\n<script>\nimport pagination from '@/components/pagination/pagination'\nimport Viewer from '@/utils/Viewer'\nexport default {\n  components: { pagination },\n  data () {\n    return {\n      token: window.sessionStorage.getItem('bbytoken'),\n      page: 1,//当前页\n      pageCount: 1,\n      tableData: [],\n      oliImgVisible: false,\n      oliImgList: [],\n      throughLoading: [],\n      rejectedLoading: []\n\n    }\n  },\n  methods: {\n\n    async init () {\n      try {\n        let res = await this.$axios.post('admin/OilExamine/regAudit', { token: this.token, page: this.page, audit_status: 0 })\n        if (res.data.code == 1) {\n          this.tableData = res.data.data.list\n          this.pageCount = res.data.data.rows;\n        } else {\n          this.tableData = []\n        }\n      } catch (err) {\n        throw (err)\n      }\n    },\n    //油站图片详情\n    oliImgDetails (item) {\n      this.oliImgVisible = true\n      this.oliImgList = item.shop_image\n      this.$nextTick(() => {\n        const img = this.$refs.oliImg;\n        Viewer(img)\n      })\n    },\n    //通过审核\n    through (item, index) {\n      this.$confirm('此操作将通过审核, 是否继续?', '提示', {\n        confirmButtonText: '确定',\n        cancelButtonText: '取消',\n        type: 'warning',\n        center: true\n      }).then(async () => {\n        try {\n\n          this.throughLoading[index] = true\n          const res = await this.$axios.post('admin/DeTravelExamine/regThrough', { token: this.token, id: item.id, tour_name: item.oil_shop_name, phone: item.leader_phone })\n          this.throughLoading[index] = false\n          if (res.data.code == 1) {\n            this.$message({ message: res.data.msg, type: \"success\" })\n            this.init()\n          } else {\n            this.$message.error(res.data.msg)\n          }\n        } catch (err) {\n          this.throughLoading[index] = false\n          throw (err)\n        }\n      }).catch(() => { });\n    },\n    //驳回\n    rejected (item, index) {\n\n      this.$prompt('请输入驳回理由', '提示', {\n        confirmButtonText: '确定',\n        cancelButtonText: '取消',\n        inputPattern: /\\S/,\n        inputErrorMessage: '请输入驳回理由'\n      }).then(async ({ value }) => {\n        try {\n\n          this.rejectedLoading[index] = true\n          const res = await this.$axios.post('admin/DeTravelExamine/regDismissal', { token: this.token, id: item.id, reason: value, phone: item.leader_phone, tour_name: item.oil_shop_name })\n          this.rejectedLoading[index] = false\n          if (res.data.code == 1) {\n            this.$message({ message: res.data.msg, type: \"success\" })\n            this.init()\n          } else {\n            this.$message.error(res.data.msg)\n          }\n        } catch (err) {\n          this.rejectedLoading[index] = false\n          throw (err)\n        }\n      }).catch(() => { });\n    },\n  },\n  mounted () {\n    this.init()\n  }\n}\n</script>\n\n<style scoped>\n.ellipsis {\n  width: 100px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-line-clamp: 2;\n  margin: 0 auto;\n  text-align: center;\n}\n</style>\n"],"sourceRoot":""}]);

